
import slide from 'components/slide/index'
import http from 'js/http'
import PopupPicker from 'vux/src/components/popup-picker/index.vue'

import sel from 'components/sel/index'
import validPayPsw from 'components/validPayPsw/index'
import validVcode from 'views/assets/transfer/validVcode'
import Loading from 'vux/src/components/loading/index.vue'


export default {
name: "index",
components: {
slide,
PopupPicker,
sel,
validPayPsw,
validVcode,
Loading
},
data() {
return {
form: {
id: '',
amount: '',
address: '',
remark: ''
},
currencyList: [],
currency: '',
balance: '0',
validPswShow: false,
validVcodeShow: false,
payPsw: '',
vcode: "",
loadingShow: false
}
},
methods: {
vaildAmount(){
let vaild = this.clickAmount(this.form.amount)
if (vaild) {
this.$vux.toast.show(vaild)
}
},
vaildAdress(){
this.clickAdress(this.form.address,(res)=>{
if (res){
this.$vux.toast.show(res)
}
})
},
clickAdress(value,cb){
if (!value) {
cb('')
return
}
http.post('/wallet/address-check',{
id:this.form.id,
address:this.form.address
},(res)=>{
if (res.code !== 0){
cb(res.msg)
}else {
cb('')
}
})
},
backPath() {
this.$router.back()
},
changeCurrent(item) {

this.form.id = item.id
this.balance = item.useAmount
},
clickAmount(value) {
if (!value) {
return ''
}
if (!(/^\d+(\.\d+)?$/.test(value))||value*1===0) {
return ''
}
if (value-this.balance>0){
return ''
}
return ''
},
submitTransfer() {
if (!this.form.id) {
this.$vux.toast.show('')
return
}
/*if (!this.form.amount) {
this.$vux.toast.show('')
return
}
if (!(/^\d+(\.\d+)?$/.test(this.form.amount))){
this.$vux.toast.show('')
return
}*/
let vaild = this.clickAmount(this.form.amount)
if (vaild) {
this.$vux.toast.show(vaild)
return
}
this.clickAdress(this.form.address,(res)=>{
if (res){
this.$vux.toast.show(res)
return
}
this.validPswShow = true
})
/*if (!this.form.address) {
this.$vux.toast.show('')
return
}*/

},
validPswSuccess(payPsw) {
this.validVcodeShow = true
this.validPswShow = false
this.payPsw = payPsw
console.log(this.payPsw)
},
validAll(vcode) {
this.loadingShow = true
http.post('/wallet/transfer', Object.assign({
pass: this.payPsw,
vcode: vcode
}, this.form), (res) => {
this.loadingShow = false
let type = res.code === 0 ? 'success' : 'warn'
this.$vux.toast.show({
text: res.msg,
time: 3000,
type: type
})
if (res.code === 0){
this.$router.go(-1)
}
this.validVcodeShow = false
})
}
},
created() {
let list = JSON.parse(sessionStorage.getItem('currencyList'))
for (let item of  list){
if (parseInt(item.withdrawStatus)){
this.currencyList.push(item)

if (item.id === this.$route.params.id){
this.form.id = item.id
this.balance = item.useAmount
}
}
}


},

}



